*{box-sizing:border-box;margin:0;padding:0;color:#000}html{height:100%;width:100%}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%;width:100%}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.App{background:url(Img/сайт.jpg);height:100vh;width:100vw;background-repeat:no-repeat;background-position:center;background-size:175vh;display:flex;align-items:center;justify-content:center}.App .container{width:25vh;height:25vh;background-image:url(Img/wooden-texture-brown-tones.jpg);background-position:center;background-size:100%;border-radius:50%;transform:translateY(-60%);display:flex;align-items:center;justify-content:center;transition:1.5s cubic-bezier(0.075, 0.82, 0.165, 1)}.App .container .clock{width:90%;height:90%;border-radius:50%;display:flex;align-items:center;justify-content:center;flex-direction:column;background-image:url(Img/clock.png);background-color:#e7e7e7;background-position-y:50%;background-position-x:50%;background-size:100%;z-index:9999}.App .container .clock .block{width:1px;height:1px}.App .container .clock .block .dot{width:1.25vh;height:1.25vh;background:red;z-index:1005;border-radius:50%;position:relative;transform:translate(-45%, -40%)}.App .container .clock .block .big-arrow{height:1vh;border-bottom:5.5vh solid #000;border-left:.9vh solid rgba(0,0,0,0);border-right:.9vh solid rgba(0,0,0,0);border-radius:35%;z-index:990;position:relative;transform:translate(-47.5%, -105%) rotate(90deg);transform-origin:50% 87%}.App .container .clock .block .middle-arrow{height:1vh;width:1vh;border-bottom:9.5vh solid #000;border-left:.6vh solid rgba(0,0,0,0);border-right:.6vh solid rgba(0,0,0,0);border-radius:100px;z-index:995;transform:translate(-47.5%, -165%);transform-origin:50% 95%}.App .container .clock .block .little-arrow{height:1vh;width:.75vh;border-bottom:11.5vh solid red;border-left:.5vh solid rgba(0,0,0,0);border-right:.5vh solid rgba(0,0,0,0);border-radius:30%;z-index:1000;position:relative;transform:translate(-50%, -234%);transform-origin:50% 95%}